<template>
    <div class="side-bar" :class="[sideShow ? 'opacity' : '']" v-show="sideShow">
        <p>Plugin List</p>
        <div id="sideBar">
            <ul>
                <li v-for="item in dataList" @iscrollTap="onClickSideBar(item)">{{ item.name }}</li>
            </ul>
        </div>
    </div>
</template>
<script>
    import IScroll from 'iscroll/build/iscroll-lite';
    export default {
        name: 'sideBar',
        data: function () {
            return {
                wrapperSideBar: null,
                dataList: [
                    {
                        path: '/index',
                        name: 'Index'
                    },{
                        path: '/list/rem',
                        name: 'Rem'
                    },{
                        path: '/list/picker',
                        name: 'Picker'
                    },{
                        path: '/list/datePicker',
                        name: 'DatePicker'
                    },{
                        path: '/list/calendar',
                        name: 'Calendar'
                    },{
                        path: '/list/alert',
                        name: 'Alert'
                    },{
                        path: '/list/confirm',
                        name: 'Confirm'
                    },{
                        path: '/list/toast',
                        name: 'Toast'
                    },{
                        path: '/list/preLoader',
                        name: 'PreLoader'
                    },{
                        path: '/list/pageLoader',
                        name: 'PageLoader'
                    },{
                        path: '/list/viewImage',
                        name: 'ViewImage'
                    },{
                        path: '/list/actionList',
                        name: 'ActionList'
                    }

                ]
            };
        },

        props: ['sideShow'],

        watch: {
            sideShow: function (val, oldVal) {
                var self = this;
                if( val ){
                    setTimeout(function () {
                        self.wrapperSideBar.refresh();
                    }, 10);
                }
            },
        },

        mounted(){
            var self = this;

            //iscroll 初始化
            setTimeout(function () {
                self.wrapperSideBar = new IScroll('#sideBar', {
                    scrollX: false,
                    scrollY: true,
                    tap: 'iscrollTap'
                });
            }, 10);
        },

        methods: {
            // 父子组件通信
            onClickSideBar: function (item) {
                this.appConfig.appRouter.push({ path: item.path });
                this.$emit('onClickSideBar');
            }
        }
    };
</script>
<style scoped>
    .side-bar {
        font-size: 0.28rem;
        position: fixed;
        left: 0;
        top: 0;
        width: 70%;
        height: 100%;
        color: #333;
        background: #F9F9F9;
        z-index: -1;
        opacity: 0;
    }
    .opacity {
        opacity: 1 !important;
    }
    .side-bar > p {
        font-size: 0.3rem;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 0.9rem;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        text-align: center;
        color: #ffffff;
        background: #555;
        z-index: 2;
    }
    .side-bar > div {
        overflow: hidden;
        font-size: 0.3rem;
        position: absolute;
        top: 0.9rem;
        left: 0;
        bottom: 0;
        width: 100%;
        background: #F9F9F9;
    }
    .side-bar > div > ul {
        min-height: 100%;
        padding: 0 0.2rem 0.4rem;
    }
    .side-bar > div > ul > li {
        line-height: 0.9rem;
        border-bottom: 1px solid #dddddd;
    }
</style>



