import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

//导入outpatient路由
import routeList from './list';

//路由
var routesArray = [
    {
        path: '/index',
        name: 'Mobile Plugins',
        component: r => require.ensure([], () => r(require('../views/index.vue')), 'pluginAPP')
    },
    // {
    //     path: '/404',
    //     name: 'Page Not Found',
    //     component: r => require.ensure([], () => r(require('../views/404.vue')), 'pluginAPP')
    // },
    // {
    //     path: '/500',
    //     name: 'Error',
    //     component: r => require.ensure([], () => r(require('../views/500.vue')), 'pluginAPP')
    // }
];

routesArray = routesArray.concat(routeList);
routesArray.push({
    path: '*',
    redirect: '/index'
});

let myRouter = new Router({
    // mode: 'history',
    // scrollBehavior: () => ({ y: 0 }),
    routes: routesArray
});

export default myRouter;



