<template>
    <div id="app">
        <div class="page" :class="[isRight ? 'fixed' : '', animation]">
            <header class="ub app-head">
                <div class="ub-f1" @click.stop="onClickShowList()"><span class="iconfont liebiao"></span></div>
                <div class="ub-f1 title">{{ pluginName }}</div>
                <div class="ub-f1"></div>
            </header>
            <div class="content" id="content">
                <router-view></router-view>
            </div>
            <div class="mask" v-show="isRight" @click.stop="onClickHideList()"></div>
        </div>

        <side-bar @onClickSideBar="onClickSideBar" :sideShow="isRight"></side-bar>
    </div>
</template>

<script>
    import sideBar from './components/sideBar.vue';

    export default {
        name: 'app',
        data(){
            return {
                pluginName: '',
                animation: '',
                isRight: false
            }
        },

        beforeMount(){
            var self = this;
            // 设置标题
            var appRouter = self.appConfig.appRouter;
            appRouter.afterEach(to => {
                if( to.name ){
                    self.pluginName = to.name;
                }
            });
        },

        components: {
            'side-bar': sideBar
        },

        methods: {
            // 显示侧边栏
            onClickShowList(){
                this.isRight = true;
                this.animation = 'animated slideOutRight';
            },

            // 隐藏侧边栏
            onClickHideList(){
                var self = this;
                self.animation = 'animated slideInRight';
                setTimeout(function () {
                    self.isRight = false;
                    self.animation = '';
                }, 400);
            },

            // sideBar子组件调用
            onClickSideBar(){
                var self = this;
                self.onClickHideList();
            }
        }

    }
</script>

<style scoped>
    @import "./assets/css/style.css";
    @import "./assets/css/main.css";
    @import "./assets/css/animate.css";

    @import "./lib/picker/picker.css";
    @import "./lib/mobileModal/mobileModal.css";
    /*@import "./assets/iconfont/publicfont/iconfont.css";*/
</style>


